import React, { Component } from 'react';
import { connect } from 'react-redux';
import SwipeableView from 'react-swipeable-views';
import MobileStepper from '@material-ui/core/MobileStepper';
import StepContent from '@material-ui/core/StepContent';
import StepConnector from '@material-ui/core/StepConnector';
import StepLabel from '@material-ui/core/StepLabel';
import Step from '@material-ui/core/Step';
import DoableForm from './components/DoableForm';
import ReviewSection from './components/ReviewSection';
import AreaSection from './components/AreaSection';

class App extends Component {

  state = {
    swipeable: true,
    sectionIndex: 0,
    reviewIndex: 0,
    areaIndex: 0
  }

  swipeToggler = {
    enableSwiping: () => this.setState({...this.state, swipeable: true}),
    disableSwiping: () => this.setState({...this.state, swipeable: false})
  }

  handleChangeIndex = index => {
    this.setState({pageIndex: index})
  }

  render() {
    const sections = [<DoableForm key="entrySection" quickEntry autofocus />];
    if (this.props.reviewables.length) sections.push(<ReviewSection key="reviewSection" reviewables={[...this.props.reviewables]} />);
    if (this.props.areas.length) sections.push(<AreaSection key="areaSection" disabled={!this.state.swipeable} swipeToggler={this.swipeToggler} />);

    return (
      <React.Fragment>
        <SwipeableView
          axis="y"
          containerStyle={{height: `${window.innerHeight}px`}}
          disabled={!this.state.swipeable}
          enableMouseEvents
          onChangeIndex={this.handleChangeIndex}
        >
          {sections}
        </SwipeableView>
      </React.Fragment>
    );
  }
}

const styles = {
  stepper: {
    style: {
      width: '100%',
      position: 'absolute',
      bottom: 0,
      justifyContent: 'center',
      backgroundColor: 'transparent'
    }
  },
  sectionStepper: {
    style: {
      width: '100%',
      position: 'absolute',
      bottom: 0,
      justifyContent: 'center',
      backgroundColor: 'transparent',
      '& > div': {
        display: 'flex',
        flexDirection: 'column'
      }
    }
  }
}

const mapStateToProps = (state) => ({
  reviewables:  state.doables.filter( doable => doable.area === ''),
  areas: [...new Set(state.doables.map( doable => doable.area ).filter( area => area !== '' ))]
});

export default connect(mapStateToProps, null)(App);
